import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";
import {
  List,
  ListItem,
  ListItemContent,
  ListItemAction,
  Icon
} from "react-mdl";
class Contact extends Component {
  state = {};
  render() {
    return (
      <div className="contact-body">
        <Grid className="contact-grid">
          <Cell col={6}>
            <img
              className="imageColour"
              src="data:image/jpeg;base64,"
            />
            <div className="contact-grid-left">
              <h1>Aditya Ravi Swami</h1>
            </div>
          </Cell>
          <Cell col={6}>
            <div className="contact-list">
              <h1>Contact Me</h1>
              <List>
                <ListItem style={{ fontSize: "40px", color: "White" }}>
                  <ListItemContent>
                    <i
                      className="fa fa-phone-square contact-list"
                      aria-hideen="true"
                    />
                    +91-4204206969{" "}
                  </ListItemContent>
                </ListItem>
                <ListItem style={{ fontSize: "50px", color: "White" }}>
                  <ListItemContent>
                    <a href="" target="_blank">
                      <i
                        className="fa fa-linkedin-square contact-list"
                        aria-hidden="true"
                      />
                    </a>
                    LinkedIn
                  </ListItemContent>
                </ListItem>
                <ListItem style={{ fontSize: "50px", color: "White" }}>
                  <ListItemContent>
                    <a href="" target="_blank">
                      <i
                        className="fa fa-github-square contact-list"
                        aria-hidden="true"
                      />
                    </a>
                    GitHub
                  </ListItemContent>
                </ListItem>
                <ListItem style={{ fontSize: "50px", color: "White" }}>
                  <ListItemContent>
                    <a href="" target="_blank">
                      <i
                        className="fa fa-facebook-square contact-list"
                        aria-hidden="true"
                      />
                    </a>
                    Facebook
                  </ListItemContent>
                </ListItem>
              </List>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Contact;
